	methods:{
			getGroupsellList(){
				this.mPost('/server/p/group/products',{
					start:this.page,
					length:1000
				}).then(res=>{
					if(res.code=1){
						if(res.data){
							let data = res.data;
							if(data.list){
								console.log(data.list);
								this.groupsellList = data.list;
							}
						}
					}
				}).catch(err=>{
					console.log(err);
				});
			},
			//跳转到商家
			goStore(){
				uni.navigateTo({
					url:"/pages/store/store"
				})
			}
	},
	components: {
		mGroupbuyList
	},
	onLoad(){
		this.getGroupsellList();
	}
